@import "../../../vars.scss";

.balance {
  width: 632px;
  @include mainSectionSize;
  &__summ {
    margin: 0 auto;
    margin-top: 16px;
    @include flexWebKit;
    justify-content: space-between;
    -webkit-justify-content: space-between;
    align-items: center;
    & span {
      font-weight: 600;
      font-size: 32px;
      line-height: 40px;
      color: #2a3135;
    }
  }
  &__line {
    /*margin-top: 20px;
    margin-bottom: 12px;*/
    height: 1px;
    width: 100%;
    background-color: #f3f5f9;
  }
  &__span {
    font-weight: 500;
    font-size: 10px;
    line-height: 16px;
    letter-spacing: 0.04em;
    text-transform: uppercase;
    color: #868c90;
  }
  &__transaction {
    margin-right: 20px;
    margin-top: 8px;
    @include flexWebKit;
    -webkit-justify-content: space-between;
    justify-content: space-between;
  }
  &__transaction-inner {
    width: 100px;
    @include flexWebKit;
    -webkit-justify-content: space-between;
    justify-content: space-between;
  }
  &__transaction-price {
    font-weight: 500;
    font-size: 12px;
    line-height: 16px;
  }
  &__transaction-price-gr {
    color: #26de81;
  }
  &__transaction-price-rd {
    color: #fc5c65;
  }
  &__transaction-date {
    font-weight: 500;
    font-size: 12px;
    line-height: 16px;
    color: #dce0e2;
    text-transform: uppercase;
  }
  &__data {
    height: 120px;
    overflow-y: scroll;
    margin-right: -20px;
  }
}

::-webkit-scrollbar {
  width: 3px;
  height: 5px;
  cursor: pointer;
}

::-webkit-scrollbar-thumb:vertical {
  height: 5px;
  background-color: #dce0e2;
  border-radius: 3px;
  cursor: pointer;
}

::-webkit-scrollbar-thumb:horizontal {
  width: 10px;
  background-color: #dce0e2;
  border-radius: 3px;
  cursor: pointer;
}

.module__article {
  @include flexWebKit;
  -webkit-justify-content: space-between;
  justify-content: space-between;
  align-items: center;
  & h2 {
    font-weight: bold;
    font-size: 20px;
    line-height: 32px;
    color: #2a3135;
    & img {
      height: 15px;
      margin-left: 5px;
    }
  }
}

@media screen and (max-width: 1150px) {
  .balance {
    width: 100%;
  }
}

@media screen and (max-width: 640px) {
  .balance {
    margin-top: 8vw;
    margin-left: 0;
    margin-right: 0;
    width: 90%;
  }
}
